"use client"

import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from "@/components/ui/accordion"
import { Container } from "@/components/ui/container"
import { Wrapper } from "@/components/ui/wrapper"
import { FadeIn } from "@/components/ui/fade-in"
import { SectionBadge } from "@/components/ui/section-badge"

const faqs = [
  {
    question: "Do I own the code and IP?",
    answer: "Yes. Unlike other agencies that license their platform to you, we build custom software that you own 100%. We hand over all source code, documentation, and IP rights upon completion."
  },
  {
    question: "How long does it take to build an AI Agent?",
    answer: "It depends on complexity. Simple automation agents can be deployed in 2-3 weeks. Complex enterprise architectures typically take 6-10 weeks from discovery to production launch."
  },
  {
    question: "Is my data secure?",
    answer: "Security is our priority. We implement enterprise-grade guardrails, PII redaction, and can deploy on your private cloud infrastructure (AWS/GCP/Azure) so your data never leaves your control."
  },
  {
    question: "Do you provide ongoing support?",
    answer: "Yes. We offer optional maintenance packages to monitor agent performance, handle model updates, and ensure 24/7 uptime. You can also choose to have your internal team manage it."
  },
  {
    question: "How is this different from ChatGPT?",
    answer: "ChatGPT is a chat interface. We build 'Agentic AI'—autonomous software that connects to your database, CRM, and APIs to perform actual work (creating invoices, scheduling meetings, analyzing live data) without human input."
  }
]

export function FAQ() {
  const jsonLd = {
    "@context": "https://schema.org",
    "@type": "FAQPage",
    "mainEntity": faqs.map((faq) => ({
      "@type": "Question",
      "name": faq.question,
      "acceptedAnswer": {
        "@type": "Answer",
        "text": faq.answer
      }
    }))
  };

  return (
    <Wrapper className="py-24 relative z-10">
      <script
        type="application/ld+json"
        dangerouslySetInnerHTML={{ __html: JSON.stringify(jsonLd) }}
      />
      <Container>
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">
          <FadeIn>
            <div className="bg-background/40 backdrop-blur-sm p-6 rounded-2xl border border-border/50 h-fit">
              <SectionBadge title="FAQ" className="mb-6" />
              <h2 className="text-3xl lg:text-4xl font-bold mb-6">
                Common Questions
              </h2>
              <p className="text-muted-foreground text-lg mb-8">
                Everything you need to know about our process and how we work.
              </p>
            </div>
          </FadeIn>

          <div className="lg:col-span-2">
            <FadeIn delay={0.2}>
              <div className="bg-background/60 backdrop-blur-md border border-border/50 rounded-2xl p-6 md:p-8 shadow-sm">
                <Accordion type="single" collapsible className="w-full">
                  {faqs.map((faq, i) => (
                    <AccordionItem key={i} value={`item-${i}`} className="border-b-border/50 last:border-0">
                      <AccordionTrigger className="text-left text-lg font-medium hover:text-primary transition-colors">
                        {faq.question}
                      </AccordionTrigger>
                      <AccordionContent className="text-muted-foreground text-base leading-relaxed">
                        {faq.answer}
                      </AccordionContent>
                    </AccordionItem>
                  ))}
                </Accordion>
              </div>
            </FadeIn>
          </div>
        </div>
      </Container>
    </Wrapper>
  )
}
